import styled from "styled-components";


export const StyledGroups=styled.section`
width: 100%;
height: 50vh;
box-shadow: 0 1px 2px ${({theme})=>theme.colors.bgcShade1};
background-color:${({theme})=>theme.colors.bgcFeed};

border-radius: 10px;
margin-top: 10px;
padding: 10px;
display: flex;
flex-wrap: wrap;

`